.footer {
  background-color: var(--color-white);
  border-top: 1px solid var(--color-gray-100);
  padding-top: 8rem;
  padding-bottom: 3rem;
}
.footer-grid {
  display: grid;
  grid-template-columns: 1fr;
  gap: 4rem;
  margin-bottom: 6rem;
}
@media (min-width: 768px) {
  .footer-grid {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1024px) {
  .footer-grid {
    grid-template-columns: 2fr 1fr 1fr 1fr 1fr;
  }
}
.footer-brand {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}
.footer-logo {
  font-family: var(--font-display);
  font-weight: 900;
  font-size: 2.25rem;
  letter-spacing: -0.05em;
  color: var(--color-black);
  transition: opacity var(--transition-base);
}
.footer-logo:hover {
  opacity: 0.7;
}
.footer-description {
  font-size: 0.875rem;
  color: var(--color-gray-400);
  line-height: 1.6;
  font-weight: 500;
  max-width: 20rem;
}
.footer-social {
  display: flex;
  gap: 1.5rem;
  padding-top: 1rem;
}
.footer-social-link {
  font-size: 0.625rem;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 0.3em;
  color: var(--color-gray-400);
  transition: color var(--transition-base);
}
.footer-social-link:hover {
  color: var(--color-black);
}
.footer-column {
  display: flex;
  flex-direction: column;
  gap: 2.5rem;
}
.footer-heading {
  font-size: 0.625rem;
  font-weight: 900;
  text-transform: uppercase;
  letter-spacing: 0.3em;
  color: var(--color-black);
  margin-bottom: 0;
}
.footer-links {
  display: flex;
  flex-direction: column;
  gap: 1.25rem;
  list-style: none;
}
.footer-links a {
  font-size: 0.75rem;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 0.15em;
  color: var(--color-gray-400);
  transition: color var(--transition-base);
  text-decoration: none;
}
.footer-links a:hover {
  color: var(--color-black);
}
.footer-newsletter-text {
  font-size: 0.75rem;
  color: var(--color-gray-400);
  font-weight: 500;
}
.footer-newsletter-form {
  position: relative;
  display: flex;
  border-bottom: 1px solid var(--color-gray-100);
  padding-bottom: 0.5rem;
  transition: border-color var(--transition-base);
}
.footer-newsletter-form:focus-within {
  border-color: var(--color-black);
}
.footer-newsletter-input {
  flex: 1;
  background-color: transparent;
  border: none;
  outline: none;
  font-size: 0.625rem;
  font-weight: 700;
  letter-spacing: 0.2em;
  text-transform: uppercase;
  padding: 0.5rem 0;
  color: var(--color-black);
}
.footer-newsletter-input::placeholder {
  color: var(--color-gray-300);
}
.footer-newsletter-submit {
  background: none;
  border: none;
  font-size: 1.25rem;
  color: var(--color-black);
  cursor: pointer;
  transition: opacity var(--transition-base);
  padding: 0 0.5rem;
}
.footer-newsletter-submit:hover {
  opacity: 0.5;
}
.footer-bottom {
  padding-top: 3rem;
  border-top: 1px solid var(--color-gray-50);
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
  align-items: center;
  font-size: 0.625rem;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 0.3em;
  color: var(--color-gray-300);
}
@media (min-width: 768px) {
  .footer-bottom {
    flex-direction: row;
    justify-content: space-between;
  }
}
.footer-copyright {
  margin: 0;
}
.footer-meta {
  display: flex;
  gap: 2rem;
}
@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes slideInUp {
  from {
    transform: translateY(100%);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}
@keyframes slideOutDown {
  from {
    transform: translateY(0);
    opacity: 1;
  }
  to {
    transform: translateY(100%);
    opacity: 0;
  }
}
@keyframes bounce {
  0%,
  100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(10px);
  }
}
@keyframes pulse {
  0%,
  100% {
    opacity: 1;
  }
  50% {
    opacity: 0.5;
  }
}
@layer base {
  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    font-family: var(--font-display);
  }
  button {
    font-family: var(--font-sans);
  }
}
@layer utilities {
  .text-balance {
    text-wrap: balance;
  }
  .animate-fade-in-up {
    animation: fadeInUp 0.6s ease-out forwards;
  }
  .animate-fade-in {
    animation: fadeIn 0.6s ease-out forwards;
  }
  .animate-slide-in-up {
    animation: slideInUp 0.3s ease-out forwards;
  }
  .animate-bounce-slow {
    animation: bounce 2s infinite;
  }
  .animate-pulse-slow {
    animation: pulse 2s infinite;
  }
}
::-webkit-scrollbar {
  width: 10px;
  height: 10px;
}
::-webkit-scrollbar-track {
  background: var(--color-gray-50);
}
::-webkit-scrollbar-thumb {
  background: var(--color-gray-300);
  border-radius: 5px;
}
::-webkit-scrollbar-thumb:hover {
  background: var(--color-gray-400);
}
::selection {
  background-color: var(--color-black);
  color: var(--color-white);
}
::-moz-selection {
  background-color: var(--color-black);
  color: var(--color-white);
}
*:focus-visible {
  outline: 2px solid var(--color-black);
  outline-offset: 2px;
}
.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  border-width: 0;
}
@media print {
  .announcement-bar,
  .footer,
  nav,
  button {
    display: none;
  }
}
